nav {
  height: 50px;
  width: 100%;
  display: flex;
  flex-direction: row;
  position: fixed;
  margin-bottom: 100px; }
  nav a {
    text-decoration: none;
    padding: 5px 8px;
    border-radius: 10px;
    color: #ffffff;
    font-weight: 900; }
  nav .leftSide {
    height: 100%;
    width: 10%;
    display: flex;
    justify-content: center;
    align-items: center; }
    nav .leftSide .iconA {
      height: 100%; }
  nav .rightSide {
    width: 90%;
    display: flex;
    align-items: center;
    justify-content: flex-end; }
    nav .rightSide .navMenuDiv {
      display: flex;
      flex-direction: row;
      width: 150px;
      justify-content: space-evenly; }
      nav .rightSide .navMenuDiv .myStory {
        transition: ease-out all 0.2s; }
        nav .rightSide .navMenuDiv .myStory:hover {
          transition: ease-out all 0.2s;
          box-shadow: inset 1px 1px 5px black; }
