@font-face {
  font-family: "Museo Sans";
  src: url("../fonts/museoSans.ttf") format("TrueType"); }

html, body {
  display: flex;
  height: 100%;
  font-family: "Museo Sans";
  cursor: default; }

.motherDiv {
  background: #02536D;
  height: 100%;
  min-height: 100%;
  overflow-y: scroll; }
  .motherDiv .mainDiv {
    width: 100%;
    height: 100%; }
    .motherDiv .mainDiv .introSegment {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100%;
      padding-left: 10%;
      padding-right: 10%;
      background: #019ED3; }
      .motherDiv .mainDiv .introSegment .introP {
        width: inherit;
        font-size: 3rem;
        align-self: center;
        color: #383838;
        font-weight: 900; }
        .motherDiv .mainDiv .introSegment .introP::before {
          content: 'My name is ';
          color: #ffffff; }
        .motherDiv .mainDiv .introSegment .introP::after {
          content: ' & I create great experiences on the internet.';
          color: #ffffff; }
    .motherDiv .mainDiv .workSegment {
      height: 400px; }
